<script>
import { computed, ref } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();

        const popover = computed(() => store.state.popover);
        const togglePopover = (menu) => store.commit("togglePopover", menu);

        const virtualProduct = computed(() => store.state.virtualProduct);
        const toggleVirtualProduct = () => store.commit("toggleVirtualProduct");

        const categoryProduct = computed(() => store.state.categoryProduct);
        const toggleCategoryProduct = () =>
            store.commit("toggleCategoryProduct");

        const popularSearch = computed(() => store.state.popularSearch);
        const togglePopularSearch = (value) =>
            store.commit("togglePopularSearch", value);

        const searchProduct = ref("");

        const resetSearch = () => {
            searchProduct.value = "";
            document.getElementById("searchBar").focus();
        };

        return {
            togglePopover,
            popover,
            togglePopularSearch,
            popularSearch,
            toggleVirtualProduct,
            virtualProduct,
            toggleCategoryProduct,
            categoryProduct,
            searchProduct,
            resetSearch,
        };
    },
};
