import { createStore } from 'vuex'

export default createStore({
    state: {
        // popoverOpen: false,
        popularSearch: false,
        virtualProduct: false,
        categoryProduct: false,
    },
    getters: {
    },
    mutations: {
        togglePopover(state, id) {
            // state[id] = !state[id]
            state.popoverOpen = !state.popoverOpen

            const popover = document.getElementById(id)


            const activePopover = document.querySelector('.bl-popover.is-active')
            // alert(activePopover)
            if (activePopover) {
                activePopover.classList.remove('is-active')
            }

            popover.classList.toggle('is-active')

            // activePopover.classList.remove('is-active')

            // popover.classList.toggle('is-active')

            // click > open > if open > close > 
        },
        togglePopularSearch(state, value) {
            state.popularSearch = value
        },
        toggleVirtualProduct(state) {
            if (state.categoryProduct) {
                state.categoryProduct = false
            }
            state.virtualProduct = !state.virtualProduct
        },
        toggleCategoryProduct(state) {
            if (state.virtualProduct) {
                state.virtualProduct = false
            }
            state.categoryProduct = !state.categoryProduct
        }
    },
    actions: {
    },
    modules: {
    }
})