<template>
    <NavbarTop />
    <Navbar />
    <section
        v-show="popularSearch"
        @click="togglePopularSearch(false)"
        class="bg-black opacity-50 fixed h-full w-full z-40"
    ></section>
    <div class="pt-44">
        <PopoverVirtualProduct />
        <PopoverCategoryProduct />
        <div class="bl-container">
            <Suspense>
                <template #default>
                  <AppCarousel />
                </template>
                <template #fallback>
                  <AppCarouselSkeleton />
                </template>
            </Suspense>
            <HomeMenu />
            <BannerGroup />
        </div>
        <Suspense>
          <template #default>
            <FlashDeal />
          </template>
          <template #fallback>
            loading......
          </template>
        </Suspense>
        <BukaMall />
        <ProductCard />
    </div>
</template>

<script>
import AppCarouselSkeleton from "@/components/AppCarouselSkeleton";
import NavbarTop from "@/components/NavbarTop";
import Navbar from "@/components/Navbar";
import HomeMenu from "@/components/HomeMenu";

import { computed, defineAsyncComponent } from "vue";
import { useStore } from "vuex";

export default {
    components: {
        AppCarouselSkeleton,
        NavbarTop,
        Navbar,
        HomeMenu,
        AppCarousel: defineAsyncComponent(() => import('@/components/AppCarousel')),
        FlashDeal: defineAsyncComponent(() => import('@/components/FlashDeal')),
        BannerGroup: defineAsyncComponent(() => import('@/components/BannerGroup')),
        PopoverVirtualProduct: defineAsyncComponent(() => import('@/components/PopoverVirtualProduct')),
        PopoverCategoryProduct: defineAsyncComponent(() => import('@/components/PopoverCategoryProduct')),
        BukaMall: defineAsyncComponent(() => import('@/components/BukaMall')),
        ProductCard: defineAsyncComponent(() => import('@/components/ProductCard')),
    },
    setup() {
        const store = useStore();

        const popularSearch = computed(() => store.state.popularSearch);
        const togglePopularSearch = (value) => store.commit("togglePopularSearch", value);

        return { togglePopularSearch, popularSearch };
    },
};
</script>
